#medusa-header {
    .trails-main {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;
        // cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .trails-text {
        position: relative;
        width: 100%;
        height: 80px;
        line-height: 80px;
        color: #fefefe;
        font-size: 5em;
        font-weight: 800;
        /* text-transform: uppercase; */
        will-change: transform, opacity;
        overflow: hidden;
    }

    .trails-text > div {
        overflow: hidden;
    }
}